import { createApp } from "vue";
import App from "@/modules/App/index.vue";
import { Skeletor } from "vue-skeletor";
import routes from "./routes";
import store from "./store";
import router from "./routes";
import { RouteLocationRaw } from "vue-router";
import { setupAuth } from "@/auth/Auth";

function callbackRedirect(appState: { targetUrl: RouteLocationRaw }) {
  router.push(appState && appState.targetUrl ? appState.targetUrl : "/login");
}

const authConfig = {
  domain: "boto.eu.auth0.com",
  client_id: "0a6dnQhl2hzRichNO6kMWo1iNtfVCkyi",
  redirect_uri: "https://botoapp.github.io/demo/callback",
  audience: "https://botoapp.github.io/demo",
};

setupAuth(authConfig, callbackRedirect).then((auth: any) => {
  createApp(App, {
    provide: {
      globalVariable: 123,
    },
  })
    .use(store)
    .use(auth)
    .use(routes)
    .component(Skeletor.name, Skeletor)
    .component("modal", {
      template: "#modal-template",
    })
    .mount("#app");
});
